footer {
    display: flex;
  font: 14px "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #777;
  padding: 14px 15px 9px;
  height: 20px;
  border-top: 1px solid #e6e6e6;
  content: "";
  position: relative;
  background-color: #ffffff;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  box-shadow: 0 1px 1px rgb(0 0 0 / 20%), 0 8px 0 -3px #f6f6f6,
    0 9px 1px -3px rgb(0 0 0 / 20%), 0 16px 0 -6px #f6f6f6,
    0 17px 2px -6px rgb(0 0 0 / 20%);
}
footer > button {
    cursor: pointer;
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    color: inherit;
    background-color: inherit;
    border: none;
    outline: none;
}
footer button:nth-child(2) {
    margin-left: calc(100% - 410px);
    margin-right: 12px;
}
footer button:nth-child(3) {
    margin-right: 12px;
}
footer button:nth-child(5) {
    margin-left: calc(100% - 475px);
    
}
.items-left {
    min-width: 67.72px;
    max-width: 67.72px;
}